<template>
    <section class="change-pwd-wrapper">
        <header-nav></header-nav>
        <div class="container">
            <div class="change-pwd-box">
                <div class="title">
                    <div class="title">修改密码</div>
                </div>
                <div class="form">
                    <div class="form-item float-l">
                        <label>原密码:<input class="user-name" v-model.trim="originPwd"
                                                      type="text"
                                                      placeholder="原密码">
                        </label>
                    </div>
                    <div class="form-item float-l">
                        <label>新密码:<input class="password" v-model.trim="newPwd"
                                                      type="password"
                                                      placeholder="新密码">
                        </label>
                    </div>
                    <div class="form-item float-l">
                        <label>请确认:<input class="password" v-model.trim="confirmPwd"
                                           type="password"
                                           placeholder="再次输入密码">
                        </label>
                    </div>
                    <span class="change-pwd-notice" :class="changePwdStatus" v-text="errMsg"></span>
                    <input class="submit" type="button" value="修改" @click="changePwd()">
                </div>
            </div>
        </div>
        <dev-footer></dev-footer>
    </section>
</template>

<script>

    import axios from 'axios'
    import md5 from 'md5'
    import headerNav from '@/components/pages/header/header'
    import devFooter from '@/components/pages/footer/footer'

    const SALT_WORK_FACTOR = 10;

    export default {
        name: 'change-pwd',
        data () {
            return {
                changePwdStatus: '',
                errMsg: '',
                originPwd: '',
                newPwd: '',
                confirmPwd: '',
                checkUserPath: 'admin/useroperation/checkPwd',
                changePwdPath: 'admin/useroperation/changePwd'
            }
        },
        computed: {},
        methods: {
            verifyForm() {
                return new Promise((resolve, reject) => {
                    let originPwd = this.originPwd;
                    let newPwd = this.newPwd;
                    let confirmPwd = this.confirmPwd;
                    if (!originPwd || !newPwd || !confirmPwd) {
                        this.errMsg = '选项不能为空';
                        reject('选项不能为空');
                    }
                    else if (newPwd !== confirmPwd) {
                        this.errMsg = '新密码与确认密码不一致';
                        reject('新密码与确认密码不一致');
                    } else {
                        resolve({originPwd, newPwd, confirmPwd});
                    }
                }).catch(err => {
                    this.changePwdStatus = 'fail';
                    return Promise.reject(err);
                })
            },
            checkUser(beforeRes) {
                let self = this;
                return new Promise((resolve, reject) => {
                    let password = md5(beforeRes.originPwd);
                    let newPwd = md5(beforeRes.newPwd);
                    axios.post(self.checkUserPath, {password})
                            .then(response => {
                                if (response.data.errno !== 0) {
                                    this.changePwdStatus = 'fail';
                                    this.errMsg = response.data.msg;
                                    reject(response.data.msg);
                                } else {
                                    resolve({newPwd});
                                }
                            })
                            .catch(err => {
                                this.changePwdStatus = 'fail';
                                reject('系统错误，修改密码失败');
                            });
                });
            },
            updatePwd(beforeRes) {
                let self = this;
                return new Promise((resolve, reject) => {
                    let newPwd = beforeRes;
                    axios.post(self.changePwdPath, newPwd)
                            .then(response => {
                                if (response.data.errno === 0) {
                                    this.changePwdStatus = "success";
                                    this.errMsg = response.data.msg;
                                    setTimeout(()=> {
                                        this.$router.replace({name: 'login'});
                                    }, 1000)
                                }
                                else {
                                    this.changePwdStatus = 'fail';
                                    this.errMsg = response.data.msg;
                                    reject(response.data.msg);
                                }
                            })
                            .catch(err => {
                                this.changePwdStatus = "fail";
                                reject('系统错误，修改密码失败');
                            })
                })
            },
            changePwd() {
                this.verifyForm().then(this.checkUser).then(this.updatePwd).catch(err => {
                    console.log(err);
                })
            }
        },
        components: {
            headerNav,
            devFooter
        },
        created()
        {

        },
        mounted()
        {

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped rel="stylesheet/less">
    .change-pwd-wrapper {
        .container {
            min-height: 560px;
            .float-l {
                float: left;
            }
            padding-top: 200px;
            .change-pwd-box {
                overflow: hidden;
                padding: 20px;
                background-color: #FFF;
                border-radius: 10px;
                border: 1px solid rgba(102, 102, 102, 0.3);
                width: 400px;
                margin: auto;
                text-align: center;
                .title {
                    font-weight: 400;
                    color: #111;
                    font-size: 24px;
                }
                .form {
                    input[type='text'], input[type='password'] {
                        padding-left: 20px;
                        border-radius: 3px;
                        border: 1px solid rgba(102, 102, 102, 0.3);
                        margin-left: 20px;
                        width: 280px;
                        height: 24px;
                    }
                    .form-item {
                        margin-top: 30px;
                        font-size: 18px;
                    }
                    .change-pwd-notice {
                        &.success {
                            color: forestgreen;
                        }
                        &.fail {
                            color: brown;
                        }
                        width: 100%;
                        font-size: 20px;
                        margin-top: 20px;
                        display: inline-block;
                    }
                    .submit {
                        cursor: pointer;
                        display: inline-block;
                        margin-top: 20px;
                        border-radius: 3px;
                        font-size: 20px;
                        color: #fff;
                        background-color: rgba(25, 139, 21, 0.8);
                        padding: 5px 30px;
                        &:hover {
                            cursor: pointer;
                            background-color: forestgreen;
                        }
                    }
                }
            }
        }
    }
</style>



