<template>
    <section class="header-nav">
        <div class="header-nav-in">
            <div class="header-content">
                <div class="head-name" @click="jump('index')">CDC后台管理系统</div>
                <div class="changePwd" @click="jump('change-pwd')">修改密码</div>
            </div>
        </div>
    </section>
</template>

<script>


    export default {
        name: 'header-nav',
        props: {},
        data () {
            return {}
        },
        methods: {
            jump(pageName) {
                this.$router.push({name: pageName});
            }
        },
        components: {},
        mounted() {

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
    .header-nav {
        width: 100%;
        position: fixed;
        top: 0;
        z-index: 2;
        background-color: black;
        .header-nav-in {
            padding: 20px;
            .header-content {
                overflow: hidden;
                .head-name {
                    cursor: pointer;
                    float: left;
                    font-size: 24px;
                    color: #e5e8eb;
                    &:hover {
                        color: #fff;
                    }
                }
                .changePwd {
                    cursor: pointer;
                    float: right;
                    font-size: 24px;
                    color: #e5e8eb;
                    &:hover {
                        color: #fff;
                    }
                }
            }
        }
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .3s
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */
    {
        opacity: 0

    }
</style>



