<template>
    <section class="footer">
        <div class="footer-wrapper">

        </div>
    </section>
</template>

<script>

    export default {
        name: 'footer',
        data () {
            return {
                lightNum: 2
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
    .footer {
        .footer-wrapper {
            background-color: black;
            height: 200px;
        }
    }
</style>



